Vue.filter('formatDate',function(str){
  if(!true) return ''
  var date = new Date(str);    //Â§ÑÁêÜÊàêËøôÁßçÊ†ºÂºèÔºöMon Oct 08 2018 15:31:59 GMT+0800 (‰∏≠ÂõΩÊ†áÂáÜÊó∂Èó¥)
  var time = parseInt( new Date().getTime() - date.getTime())/1000  //Áé∞Âú®ÁöÑÊó∂Èó¥ÂáèÂéª‰º†ÂÖ•ÁöÑÊó∂Èó¥
  if(time < 30){
    return 'ÂàöÂàö'
  }else if( time < 60){
    return parseInt(time) + 'ÁßíÂâç'
  }else if( time/60 < 60){    //3600Áßí‰∏Ä‰∏™Â∞èÊó∂
    return parseInt(time/60) + 'ÂàÜÈíüÂâç'
  }else if( time/60/60 < 24){
    return parseInt(time/60/60) + 'Â∞èÊó∂Ââç'
  }else if(time/60/60/24 < 30){
    return parseInt(time/60/60/24) + 'Â§©Ââç'
  }else if(time/60/60/24/30 < 12){
    return parseInt(time/60/60/24/30)+'ÊúàÂâç'
  }else{
    return parseInt(time/60/60/24/30/12) +'Âπ¥Ââç'
  }
})
