<template>
  <div >
    <div class="a-topic">
      <span class="a-top" v-if="articleInf.top">ÁΩÆÈ°∂</span>
      <span v-html="articleInf.title" class="a-title"></span>
      <ul class="a-ul">
        <li> ‚Ä¢ ÂèëÂ∏É‰∫é {{articleInf.create_at | formatDate}} &nbsp; </li>
        <li v-if="articleInf.author.loginname"> ‚Ä¢ ‰ΩúËÄÖ {{articleInf.author.loginname}}&nbsp;</li>
        <li> ‚Ä¢ {{articleInf.visit_count}}&nbsp;Ê¨°ÊµèËßà&nbsp;</li> 
        <li> ‚Ä¢ Êù•Ëá™ {{ articleInf | ArticleType}}</li>
      </ul>
      
    </div>
    <div class="content">
      <span v-html="articleInf.content"></span>
    </div>
    <div class="replyList">
      <div class="repliesNum">{{articleInf.replies.length}}ÂõûÂ§ç</div>
      <div class="reply" v-for="(reply,index) in articleInf.replies" :key="reply.id">
        <router-link :to="{
          name:'userinfo',
          params:{
            'username': reply.author.loginname,
          }
        }">
          <img class="ava" :src="reply.author.avatar_url" alt="">  
          <span class="username">{{reply.author.loginname}}</span>
        </router-link>
        <span class="replyInfo">{{index+1}}Ê•º ‚Ä¢{{reply.create_at | formatDate}}</span>
        <span v-html="reply.content"></span>
        <span  class="zan" v-if="reply.ups.length>0">üëç{{reply.ups.length}}</span>
      </div>
 
    </div>
  </div>
</template>

<script>
export default {
  name: "Article",
  data() {
    return {
      articleInf: {}
    };
  },
  methods: {
    getData() {
      this.$http
        .get(`https://cnodejs.org/api/v1/topic/${this.$route.params.id}`)
        //$route‰∏≠ÊúâÂú®Ë∑≥ËΩ¨Êó∂ÈìæÊé•‰ø°ÊÅØÔºåÂåÖÊã¨fullPath  name  paramsÁ≠â„ÄÇ
        .then(res => {
          this.articleInf = res.data.data;
        });
    }
  },
  created() {
    this.getData();
  },
  watch:{
    '$route'(to,from){
      this.getData();
    }
  }
};
</script>

<style >
@import "../assets/index.min.23a5b1ca.min.css";
.replyInfo{
  font-size: 12px;
  color:rgb(0,136,204);
}
.repliesNum {
  height: 12px;
  background: rgb(246, 246, 246);
  font-size: 12px;
  padding: 14px 14px;
}
.username{
  font-size: 10px;
  color: rgb(102, 102, 102);
}
a{
  text-decoration: none;
  color: rgb(0,136,204)
}
.reply {
  background: rgb(244, 252, 240);
  padding: 10px 10px;
  border-radius: 2px;
  border-top: 1px solid rgb(240, 240, 240);
  position: relative;
}
.zan {
  position: absolute;
  top: 14px;
  right: 14px;
}
.replyList {
  margin-top: 13px;
  border-radius: 2px;
}
.replyList .ava {
  width: 30px;
  height: 30px;
}
.content {
  background: white;
  padding: 20px;
  border-top: 1px solid #ccc;
}
.a-topic {
  box-sizing: border-box;
  height: 88px;
  background: white;
  border-top-left-radius: 3px;
  border-top-right-radius: 3px;
  padding: 10px 10px;
  vertical-align: center;
}
.a-top {
  margin: 10px 5px;
  display: inline-block;
  padding: 1px 2px;
  background: rgb(128, 189, 1);
  color: white;
  border-radius: 2px;
  font-size: 12px;
}
.a-title {
  font-size: 22px;
  font-weight: 700;
  margin: 8px 0;
  display: inline-block;
  vertical-align: bottom;
  width: 75%;
  line-height: 130%;
}
.a-ul {
  margin: 5px 5px 5px 0;
  padding-left: 0;
  padding-bottom: 14px;
  list-style: none;
  display: flex;
}
.a-ul li {
  font-size: 12px;
  color: rgb(131, 131, 131);
}
</style>



