<template>
<div class="wrapper-wrapper">
<div id="inf-wrapper">
      <div id="top">
        <span id="all" class="all current-tab">ÂÖ®ÈÉ®</span>
        <span id='good'>Á≤æÂçé</span>
        <span id="share">ÂàÜ‰∫´</span>
        <span id="ask">ÈóÆÁ≠î</span>
        <span id="job">ÊãõËÅò</span>
        <span id="dev">ÂÆ¢Êà∑Á´ØÊµãËØï</span>
      </div>
      <div id="inf" v-for="article in articleList" :key='article.id'>
          <div id="infWord">
            <img :src="article.author.avatar_url" alt="123" >
            <p>
              <span id="reply">{{article.reply_count}}</span><span id="a">/</span><span id="click">{{article.visit_count}}</span>  
            </p>
            <span id="type" :class="{top:article.top === true,
            good:article.good === true,
            normal:(article.good !== true && article.top !== true)}">{{article | ArticleType}}</span>
            <router-link :to="{name:'article',params:{
              id: article.id,
              name:article.author.loginname
            }}">
              <span id="title">{{article.title}}</span>
            </router-link>
            <span id="date">{{article.last_reply_at | formatDate}}</span> 
          </div>
      </div>
    </div>
    <pagination @toParent='receive'/>
</div>
    

</template>

<script>
import pagination from "../components/pagination";


export default {
  name: "postlist",
  components: {
    pagination
  },
  data() {
    return {
      articleList: [],
      getPage: 1
    };
  },
  methods: {
    getData() {
      this.$http
        .get("https://cnodejs.org/api/v1/topics", {
          params: {
            page: this.getPage,
            limit: 20
          }
        })
        .then(res => {
          this.articleList = res.data.data;
        });
    },
    receive(value) {
      this.getPage = value;
      this.getData();
    }
  },
  beforeMount() {
    this.getData();
  },
  mounted(){
    this.$('#top').on('click','span',(e)=>{
      console.log(e.currentTarget.id)
      this.$(e.currentTarget).addClass('current-tab').siblings().removeClass('current-tab')
      this.$http
        .get(`https://cnodejs.org/api/v1/topics?tab=${e.currentTarget.id}`, {
          params: {
            page: this.getPage,
            limit: 20
          }
        })
        .then(res => {
          this.articleList = res.data.data;
          console.log(res.data)
        });
    })

  }
};
</script>

<style scoped>

.wrapper-wrapper {
  padding-bottom: 60px;
}
a {
  text-decoration: none;
}
#date {
  font-size: 12px;
  color: rgb(119, 128, 135);
  float: right;
}
p {
  display: inline-block;
  width: 70px;
  text-align: center;
  margin: 0;
}
#a {
  color: black;
  font-size: 14px;
}
.top,
.good {
  padding: 1px 2px;
  background: rgb(128, 189, 1);
  color: white;
  border-radius: 2px;
}
.normal {
  padding: 1px 2px;
  background: rgb(229, 229, 229);
  color: rgb(153, 153, 153);
  border-radius: 2px;
}
#top {
  border: 1px solid transparent;
  border-top-left-radius: 4px;
  border-top-right-radius: 4px;
}
#top span {
  cursor: pointer;
  margin: 8px 8px;
  color: rgb(128, 189, 1);
  font-size: 14px;
  padding: 1px 3px;
}
#top span:nth-child(1) {
  margin-left: 24px;
}
#top span.current-tab {
  background: rgb(128, 189, 1);
  color: white;
  border-radius: 2px;
}
#reply {
  font-size: 14px;
  color: rgb(158, 120, 192);
}
#click {
  font-size: 14px;
  color: rgb(180, 180, 180);
}
#type {
  font-size: 13px;
}
#title {
  color: #333;
}
#inf-wrapper {
  background: rgb(255, 255, 255);
  border: 1px solid rgb(225, 225, 225);
  /* border-top-left-radius: 4px;
  border-top-right-radius: 4px; */
  border-radius: 4px;
}
* {
  box-sizing: border-box;
}
img {
  width: 30px;
  height: 30px;
  display: inline-block;
  vertical-align: middle;
}
#top {
  height: 40px;
  padding-top: 10px;
  background-color: rgb(246, 246, 246);
}

#inf {
  padding: 10px;
  height: 50px;
  border-bottom: 1px solid rgb(240, 240, 240);
}
#inf:last-child {
  border-radius: 4px;
}
#infWord {
  display: inline;
  line-height: 30px;
}
</style>



