<template>
  <div>
    <section class="userinfo">
      <div class="u-top"><span>È¶ñÈ°µ</span>/</div>
      <div class="img-name">
        <img :src="ui.avatar_url" alt="">
        <span>{{ui.loginname}}</span>
      </div>
      <div class="score">{{ui.score}}ÁßØÂàÜ</div>
      <div class="git" v-if="ui.githubUsername">Github:<a :href="`https://github.com/${ui.githubUsername}`">@{{ui.githubUsername}}</a></div>
      <span class="register-time">Ê≥®ÂÜåÊó∂Èó¥{{ui.create_at | formatDate}}</span>
    </section>

    <section class="create">
      <div>ÊúÄËøëÂàõÂª∫ÁöÑËØùÈ¢ò</div>
      <div class="list" v-for="item in ui.recent_topics" :key="item.id">
        <router-link :to='{name:"userinfo",params:{username: item.author.loginname}}'>
          <img :src="item.author.avatar_url" width="30px"/>
        </router-link>
        <router-link :to="{name:'article',params:{id:item.id}}">
          {{item.title}}
        </router-link>
        <span class="time">{{item.last_reply_at | formatDate}}</span>
      </div>
    </section>
    <section class="join">
      <div>ÊúÄËøëÂèÇ‰∏éÁöÑËØùÈ¢ò</div>
      <div class="list" v-for="item in ui.recent_replies" :key="item.id">
        <router-link :to='{name:"userinfo",params:{uername: item.author.loginname}}'>
          <img :src="item.author.avatar_url" width="30px"/>
        </router-link>
        <router-link :to="{name:'article',params:{id:item.id}}">
          {{item.title}}
        </router-link>
          <span class="time">{{item.last_reply_at | formatDate}}</span>
      </div>
    </section>
  </div>  
</template>

<script>
export default {
  name: "userinfo",
  data() {
    return {
      ui: {}
    };
  },
  methods: {
    getData() {
      this.$http
        .get(`https://cnodejs.org/api/v1/user/${this.$route.params.username}`)
        .then(res => {
          this.ui = res.data.data;
          console.log(this.ui);
        });
    }
  },
  beforeMount() {
    this.getData();
  }
};
</script>

<style scoped>
a{
  text-decoration: none;
  color: rgb(0,136,204);
}
.list{
  height: 50px;
  border-bottom: 1px solid rgb(240, 240, 240);
}
.list img,span{
  margin:10px 10px;
  vertical-align: middle;
}
.time{
  font-size: 12px;
  float: right;
  margin: 14px 8px;
}
section {
  margin-bottom: 14px;
}
section div:last-child{
  border: none;
}
section div:nth-child(1) {
  box-sizing: border-box;
  height: 40px;
  background: rgb(246, 246, 246);
  border-bottom: 1px solid rgb(229, 229, 229);
}
.create div:first-child,.join div:first-child{
  padding: 8px 8px;

}
.create div {
  border-bottom: 1px solid rgb(240, 240, 240);
}
.u-top span {
  color: rgb(128, 189, 1);
  display: inline-block;
}
.userinfo {
  height: 300px;
  font-size: 14px;
  color: rgb(119, 128, 135);
}
.userinfo div {
  margin-top: 8px;
  margin-bottom: 8px;
}
.register-time {
  margin-left: 10px;
}
.score {
  margin-left: 10px;
}
.git {
  margin-left: 10px;
}

section {
  background: #fff;
}
.img-name img {
  height: 40px;
  width: 40px;
  padding: 10px;
  vertical-align: middle;
}
</style>



