<template>
  <div class="wrapper">
    <div class="authorInfo">
      <div class="top">‰ΩúËÄÖ</div>
      <div class="inner">
        <router-link :to="{name:'userinfo',params:{username:info.loginname}}">
          <span>
            <img :src="info.avatar_url" alt="" width="48px">
          </span>
          <span>{{info.loginname}}</span>
        </router-link>
        <div>ÁßØÂàÜ{{info.score}}</div>
      </div>
    </div>
    <div class="authorTopic">
      <div class="top">‰ΩúËÄÖÂÖ∂ÂÆÉËØùÈ¢ò</div>
      
      <div class="title" v-for="item in info.recent_topics" :key="item.id">
        <router-link :to="{name:'article',
          params:{name: item.author.loginname,id: item.id}}"> 
            {{item.title}}
        </router-link>
    </div>
  </div>
  </div>
</template>

<script>
export default {
  name: "slidebar",
  data() {
    return {
      info: {}
    };
  },
  methods: {
    getData() {
      this.$http
        .get(`https://cnodejs.org/api/v1/user/${this.$route.params.name}`)
        .then(res => {
          this.info = res.data.data;
          console.log(this.info);
        });
    }
  },
  watch: {
    $route(to, from) {
      //Âà∑Êñ∞ÂèÇÊï∞ÊîæÂà∞ËøôÈáåÈáåÈù¢ÂéªËß¶ÂèëÂ∞±ÂèØ‰ª•Âà∑Êñ∞Áõ∏ÂêåÁïåÈù¢‰∫Ü
      console.log(this.$route.path)
      
      this.getData(this.$route.path);
    }
  },
  beforeMount() {
    this.getData();
  }
};
</script>

<style scoped>
.authorTopic {
  width: 300px;
}
.title {
  background: white;
  box-sizing: border-box;
  padding: 10px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
img {
  vertical-align: middle;
  padding-bottom: 10px;
}
.title {
  width: 300px;
}
.inner {
  padding: 10px 10px;
  width: 300px;
  vertical-align: middle;
}
.wrapper {
  font-size: 14px;
  position: absolute;
  float: right;
  right: 70px;
  left: 990px;
}
.authorInfo {
  margin-bottom: 16px;
  background: white;
  width: 300px;
}
.top {
  height: 40px;
  line-height: 40px;
  padding-left: 10px;
  background: rgb(246, 246, 246);
}
</style>



