<template>
  <div class="btnList">
    <button @click="changeBtn">È¶ñÈ°µ</button>
    <button @click="changeBtn">‰∏ä‰∏ÄÈ°µ</button>
    <button 
    @click="changeBtn(btn)" v-for="btn in btns"
    :class="{currentPage: btn=== currentPage, default: true}"
    >{{btn}}
    </button>
    <button @click="changeBtn">‰∏ã‰∏ÄÈ°µ</button>
  </div>
</template>

<script>
import $ from 'jquery'
export default {
  name:'pagination',
  data(){
    return {
      btns:[1,2,3,4,5,'......'],
      currentPage:1
    }
  },
  methods:{
    changeBtn(value){
      if(value instanceof MouseEvent){
        switch(value.target.innerText){
          case '‰∏ä‰∏ÄÈ°µ':
            $('button.currentPage').prev().click();
            break;
          case '‰∏ã‰∏ÄÈ°µ':
            $('button.currentPage').next().click();
            break;
          case 'È¶ñÈ°µ':
              this.btns = [1,2,3,4,5,'......'];
            this.changeBtn(1);
            break;
          default:
            break;  
        } 
        return
      }
      this.currentPage = value
      if(value === this.btns[4]){
        this.btns.shift()
        this.btns.splice(4,0,++value)
      }else if(value === this.btns[0] && value > 1){
        this.btns.unshift(--value)
        this.btns.splice(5,1)
      }
      this.$emit('toParent',this.currentPage)
    }
  }
}
</script>

<style scoped>
  .currentPage{

  }
  .btnList{
    box-sizing: border-box;
    border: 1px solid rgb(225, 225, 225);background: white;
    border-radius: 4px;
    width: 590px;
    margin: 10px;
    padding: 10px;
  }
  button{
    background: white;
    width: 60px;
    height: 30px;
    border: 1px solid rgb(221, 221, 221);
    border-radius: 3px;
  }
</style>



